.gamma-pnl-simulate {
  width: 100%;
  height: 100%;
  // background: linear-gradient(106.26deg, rgba(85, 91, 237, 0.05) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(97, 122, 255, 0.1) 100.82%, );

  input {
    border: 0;
    outline: none;
    background-color: transparent;
    color: #E0ECFF;
    width: 30px;
    font-weight: 600;
    font-size: 16px;
  }




  .gamma-pnl-simulate-header {
    min-height: 83px;
    border-bottom: 1px solid #203B60;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 24px 0 24px;
    position: relative;

    .open-modal-gamma-btn {
      width: 171px;
      height: 48px;
      background: #203B60;
      border: 1px solid rgba(89, 174, 153, 0.5);
      border-radius: 4px;
      font-weight: 700;
      font-size: 14px;
      color: #59AE99;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
    }

    .m-open-modal-gamma-btn {
      display: none;
    }

    .pnl-simulate-tab {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 240px;
      font-weight: 700;
      font-size: 14px;
      color: #E0ECFF;
      position: relative;
      z-index: 1;

      .simulate-btn {
        width: 115px;
        height: 48px;
        background: #203B60;
        border-radius: 4px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
      }

      .active {
        border: 1px solid rgba(224, 236, 255, 0.5);
      }
    }

    .advanced-settings-box {
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      color: #E0ECFF;
      font-weight: 600;
      height: 53px;

      .condition-entry {
        display: flex;
        align-items: flex-end;
        // justify-content: space-between;
        color: #E0ECFF;

        .entry-price-box,
        .entry-volume-box {

          .entry-price-title,
          .volume-price-title {
            font-weight: 700;
            font-size: 14px;
            margin-bottom: 4px;
          }

          .check-use-index-entry-price {
            display: flex;
            align-items: center;
            justify-content: center;

            .use-index-price,
            .input-index-price {
              width: 160px;
              height: 32px;
              cursor: pointer;
              background: rgba(85, 119, 253, 0.1);
              border-radius: 4px;
              display: flex;
              align-items: center;
              justify-content: space-between;
              padding: 0 14px;

              .check-in {
                width: 16px;
                height: 16px;
                border: 2px solid rgba(147, 161, 184, 0.2);
                border-radius: 2px;
              }
            }

            .input-price::placeholder {
              font-size: 12px;
            }

            .input-index-price {
              .input-price {
                width: 100%;
              }
            }
          }

          .input-entry-volume {
            width: 160px;
            height: 32px;
            background: rgba(85, 119, 253, 0.1);
            border-radius: 4px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 0 14px;
            white-space: nowrap;

            .input-volume {
              width: 50px;
            }
          }

        }

        .entry-volume-box,
        .reset-condition,
        .apply-condition {
          margin-left: 12px;
        }

        .reset-condition,
        .apply-condition {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 56px;
          height: 32px;
          background: #203B60;
          border-radius: 4px;
          font-size: 12px;
          cursor: pointer;
          color: #93A1B8;

          img {
            margin-right: 4px;
          }
        }

        .apply-condition:hover,
        .reset-condition:hover {
          color: #E0ECFF;
        }

      }



      .advanced-settings-btn {
        margin-left: 28px;
        background: rgba(55, 125, 255, 0.1);
        border-radius: 4px;
        width: 178px;
        cursor: pointer;
        height: 48px;
        font-weight: 700;
        font-size: 14px;
        color: #E0ECFF;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;

        img {
          margin-right: 8px;
        }
      }

    }

    .m-settings {
      display: none;
    }
  }

  .gamma-pnl-simulate-chart-info {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    // flex-wrap: wrap;
    padding-bottom: 14px;
    height: calc(100% - 109px; );
    padding-right: 38px;
    padding-left: 31px;
    position: relative;


    .chart-mask {
      width: 100%;
      height: 100%;
      display: flex;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      align-items: center;
      justify-content: center;
      background: linear-gradient(180deg, rgba(10, 23, 43, 0.245) 0%, rgba(10, 23, 43, 0.5) 54.4%);
      backdrop-filter: blur(7px);
    }

    .pnl-chart {
      min-width: 450px;
      height: 335px;
      position: relative;
      flex-grow: 1;

      .long-short {
        position: absolute;
        top: -4px;
        right: 0;
        display: flex;
        width: 125px;
        height: 24px;
        border-radius: 6px;
        align-items: center;
        justify-content: space-between;
        font-size: 10px;

        .long,
        .short {
          width: 50%;
          height: 100%;
          padding: 0 12px;
          border: 1px solid #203B60;
          display: flex;
          justify-content: space-between;
          align-items: center;
          cursor: pointer;
          font-weight: 500;

          img {
            margin-top: 4px;
          }
        }

        .long {
          border-right: 1px solid transparent;
          border-radius: 6px 0 0 6px;
        }

        .short {
          border-left: 1px solid transparent;
          border-radius: 0 6px 6px 0;
        }

        .long.check {
          border: 1px solid rgba(224, 236, 255, 0.5);
          color: #FFF;
          background-color: #203B60;
        }

        .short.check {
          border: 1px solid rgba(224, 236, 255, 0.5);
          color: #FFF;
          background-color: #203B60;
        }

        .my-position-direction {
          width: 138px;
          height: 24px;
          font-size: 12px;
          font-weight: 600;
          border-radius: 4px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 0 8px;

          span {
            color: #E0ECFF;
          }
        }

        .long-direction {
          background: rgba(89, 174, 153, 0.1);
          border: 1px solid #59AE99;
          color: #59AE99;
        }

        .short-direction {
          background: rgba(206, 94, 94, 0.1);
          border: 1px solid #CE5E5E;
          color: #CE5E5E;
        }

      }

      .max,
      .min {
        font-size: 14px;
        color: #FFF;
        font-weight: 600;
      }

      .max {
        margin-bottom: 4px;
      }

      .min {
        margin-top: 4px;
      }

      .chart-pnl-box {
        width: 100%;
        height: 259px;
        margin-top: 18px;
        .active-dot-bg{
          width: 8px;
          height: 8px;
          border-radius: 50%;
          border: 2px solid #FFF;
        }
      }

      .recharts-tooltip-wrapper {
        width: 175px;
        height: 66px;
        visibility: visible !important;
      }

      .tip-chart {
        width: 105px;
        height: 59px;
        background-color: #1A2D4A;
        border-radius: 8px;
        font-size: 12px;
        .hr-chart-tip{
          height: 1px;
          width: 200px;
          position: absolute;
          background-color: #FFF;
        }

        .price-chart-box {
          display: flex;
          justify-content: space-between;
          align-items: center;
          height: 59px;
          padding: 0 14px;
          margin-top: 8px;

          .tip-market-name,
          .tip-index-name {
            display: flex;
            align-items: center;
            margin-bottom: 8px;
          }

          .entry-index-price-tip,
          .cur-index-price-tip {
            width: 10px;
            height: 10px;
            border: 2px solid #FFF;
            border-radius: 50%;
            margin-right: 4px;
          }


          .cur-index-price-tip {
            background-color: #CE5E5E;

          }
          .long-color {
            background-color: #59AE99;
          }
          .entry-index-price-tip {
            background-color: #5577FD;
          }

          .tip-market-price,
          .tip-index-price {
            font-size: 14px;
            color: #FFF;
          }
        }

      }

      .default {
        position: absolute !important;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }

    .info-change {
      width: 590px;
      height: 330px;
      margin-left: 24px;
      overflow: auto;
      -ms-overflow-style: none;
      /* Internet Explorer 10+ */
      scrollbar-width: none;

      /* Firefox */
      &::-webkit-scrollbar {
        display: none;
        /* Safari and Chrome */
      }


      .entry-price-change {
        font-size: 16px;
        color: #93A1B8;
        margin-top: 24px;

        span {
          font-size: 24px;
          color: #FFF;

          .chart-index-price {
            font-size: 20px;
            color: #93A1B8;
          }
        }
      }

      .other-info {
        margin-top: 30px;

        .other-info-title {
          display: flex;
          align-items: flex-end;
          justify-content: space-between;
          font-size: 16px;
          color: #93A1B8;

          .greeks-box {
            width: 154px;
          }

          .payout-box {
            width: 227px;
          }

          .margin-box {
            width: 193px;
          }

          .payout-box,
          .greeks-box,
          .margin-box {
            padding-bottom: 8px;
            border-bottom: 1px solid #203B60;
          }

        }

        .other-info-box {
          display: flex;
          justify-content: space-between;
          flex-wrap: wrap;
          align-items: center;
          // margin-top: 10px;

          .other-info-box-detail {
            width: 154px;
            height: 72px;
            background: rgba(85, 119, 253, 0.1);
            border-radius: 4px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 12px;
            margin-top: 10px;

            .other-info-box-detail-num {
              color: #E0ECFF;
              display: flex;
            }
          }

          .funding-simulate {
            width: 227px;
            flex-direction: initial;

            div {
              display: flex;
              flex-direction: column;
              justify-content: space-between;
            }

            .other-info-box-detail-num {
              flex-direction: initial;
              align-items: center;
              margin-top: 8px;

              .funding-days {
                input {
                  height: 24px;
                  font-size: 14px;
                  background: rgba(43, 58, 89, 0.5);
                  padding-left: 4px;
                  border-radius: 4px;
                }
              }

              img {
                cursor: pointer;
              }

              .funding-remove-btn {
                margin-top: 4px;
              }
            }
          }

          .pnl-detail {
            width: 227px;
          }

          .maintainence-initial {
            width: 193px;
          }
        }
      }
    }

  }

}

@media only screen and (max-width:1530px) {
  .gamma-pnl-simulate {
    .gamma-pnl-simulate-chart-info {
      flex-wrap: wrap;

      .pnl-chart {
        width: 100%;
        max-width: 100%;
        min-width: 350px;
      }

      .info-change {
        width: 100%;

        .other-info {
          width: 590px;
        }
      }
    }
  }
}

@media only screen and (max-width:1568px) {
  .gamma-pnl-simulate {
    .gamma-pnl-simulate-header {
      align-items: flex-start;
      padding-top: 14px;

      .pc-settings {
        display: none;
      }

      .m-open-modal-gamma-btn {
        display: flex;
        position: absolute;
        right: 180px;
        top: 14px;
        height: 57px;
        margin-right: 4px;

      }

      .m-settings {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        position: absolute;
        right: 28px;
        width: calc(100% - 50px);
        height: 140px;

        .border-box {
          width: calc(100% - 148px);
          height: 54px;
          position: absolute;
          left: -1px;
          background: #0A172B;
          border: 1px solid #203B60;
          border-radius: 0 0 4px 0;
          border-left: none;
          border-top: none;
        }

        .advnced-settings-btn {
          width: 145px;
          height: 58px;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-wrap: nowrap;
          white-space: nowrap;
          border: 1px solid #203B60;
          background-color: #377dff1a;
          border-bottom: 0;
          border-radius: 4px 4px 0 0;
          font-size: 14px;
          font-weight: 700;
          cursor: pointer;
          z-index: 1;
          padding-left: 12px;
        }

        .condition-entry {
          width: 100%;
          height: 88px;
          align-items: center;
          padding-right: 8px;
          padding-left: 14px;
          border: 1px solid #203B60;
          border-radius: 4px 0 4px 4px;
          background: rgba(55, 125, 255, 0.1);

          .volume-apply-reset-box {
            display: flex;
            align-items: center;
          }

          .apply-condition,
          .reset-condition {
            margin-top: 20px;
            color: #93A1B8;

            img {
              margin-right: 4px;
            }
          }

          .apply-condition:hover,
          .reset-condition:hover {
            color: #E0ECFF;
          }
        }
      }


      .m-no-openCondition {
        width: 146px;
        height: 57px;
        border: 0;

        .advnced-settings-btn {
          border: 1px solid transparent;
          border-radius: 4px;
        }
      }
    }

    .openCondition-header {
      height: 166px;
    }

    .gamma-pnl-simulate-chart-info {
      padding-top: 24px;
      height: calc(100% - 83px);
    }

    .openCondition-chart-info {
      height: calc(100% - 166px; );
    }

  }
}

@media only screen and (max-width:760px) {
  .gamma-pnl-simulate {
    .gamma-pnl-simulate-header {
      padding-top: 20px;

      .pnl-simulate-tab {
        width: 200px;

        .simulate-btn {
          width: 96px;
        }
      }

      .m-settings {
        width: calc(100% - 50px);
        height: 204px;

        .advnced-settings-btn {
          height: 54px;
        }

        .border-box {
          width: calc(100% - 148px);
        }

        .condition-entry {
          height: 152px;
          flex-wrap: wrap;

          .entry-price-box,
          .entry-volume-box {
            margin-left: 0;

            .check-use-index-entry-price {

              .input-entry-volume,
              .use-index-price,
              .input-index-price {
                width: 146px;
                background: #203B60;
              }
            }

            .input-entry-volume {
              width: 146px;
              background: #203B60;
              font-size: 14px;

              .input-volume {
                width: 42px;
              }
            }

          }

          .entry-price-box {
            margin-right: 12px;
          }

          .apply-condition,
          .reset-condition {
            margin-top: 19px;
          }
        }
      }

      .m-no-openCondition {
        width: 145px;
        height: 54px;
        border: 0;
        background: rgba(55, 125, 255, 0.1);
      }

    }

    .openCondition-header {
      height: 247px;
    }

    .gamma-pnl-simulate-chart-info {
      padding: 24px;
      height: calc(100% - 247px);
      margin-top: 4px;

      .info-change {
        height: 286px;
      }
    }
  }
}

@media only screen and (max-width: 1140px) {
  .gamma-pnl-simulate .gamma-pnl-simulate-header .m-open-modal-gamma-btn {
    display: none;
  }
}

@media only screen and (max-width: 410px) {
  .gamma-pnl-simulate .gamma-pnl-simulate-header .m-no-openCondition {
    width: 115px;
  }

  .gamma-pnl-simulate .gamma-pnl-simulate-header .m-settings .advnced-settings-btn {
    width: 115px;
    white-space: inherit;
  }
}