@-webkit-keyframes warnningfadeIn {
  from {
    opacity: 0;
    height: 0;
  }

  to {
    opacity: 1;
    height: 100%;
  }
}

@keyframes warnningfadeIn {
  from {
    height: 0;
    opacity: 0;
  }

  to {
    height: 100%;
    opacity: 1;
  }
}


@keyframes warnningfadeOut {
  from {
    opacity: 0;
    height: 100%;
  }

  to {
    height: 0;
    opacity: 1;
  }
}

@-webkit-keyframes warnningfadeOut {
  from {
    height: 100%;
    opacity: 0;
  }

  to {
    height: 0;
    opacity: 1;
  }
}

.leverage-bar {
  display: flex;
  flex-direction: column;
  margin: 0 24px 0 48px;
  // min-height: 586px;

  .gamma-special {
    margin: 24px 0px 0px;
    display: none;

    .customize {
      display: none;
    }
  }

  &.gamma {
    .input-area {
      .l-input:first-child {
        width: 100%;

        input {
          width: auto;
        }
      }

      .cutting-line,
      .l-input:last-child {
        display: none;
      }
    }

    .gamma-special {
      display: block;

      .default {
        display: flex;
        justify-content: space-between;
        color: #DFE9E6;
        font-size: 14px;

        .arrow-icon {
          cursor: pointer;
        }
      }

      .customize.show {
        display: flex;

        .selected-icon {
          display: block;
        }
      }

      .customize.hide {
        .selected-icon {
          display: none;
        }
      }

      .customize {
        justify-content: space-between;
        font-size: 14px;
        color: #DFE9E6;
        height: 48px;
        margin: 16px 0;

        .left {
          cursor: pointer;
          &.hide {
            .selected-icon {
              display: none;
            }
          }

          display: block;
          padding: 0 16px;
          display: flex;
          justify-content: space-between;
          width: 48%;
          align-items: center;
          background: rgba(89, 174, 153, 0.1);
          border-radius: 2px;
        }

        .right {
          width: 100%;

          input::placeholder {
            font-size: 14px;
            // color: #DFE9E6;
          }

          input {
            color: #DFE9E6;
            width: 100%;
            font-size: 14px;
          }

          .unit {
            display: none;
          }

          .entry-price-input {
            height: 100%;
          }
        }
      }
    }
  }

  .input-area {
    display: flex;
    justify-content: space-between;

    .l-input {
      width: 49%;

      input {
        font-size: 14px;
      }
    }

    .cutting-line {
      height: 10px;
      margin: auto;
    }
  }

  .parsed-volume {
    height: 20px;
    display: none;
    font-size: 14px;
    margin-top: 16px;
  }

  .parsed-volume.show {
    display: block;
  }

  .quick-input {
    .l-button-group {
      .g-button {
        width: 24%;
      }
    }

    .input-tip {
      margin: 16px 0;
      font-size: 14px;
      visibility: hidden;

      .number {
        color: #DFE9E6;
        white-space: nowrap;
      }
    }

    .input-tip.show {
      visibility: visible;
    }

    .input-helper {
      display: flex;
      justify-content: space-between;
      margin: 16px 0;

      .close-only {
        visibility: hidden;
      }

      .close-only.show {
        visibility: visible;
      }

      .clear {
        cursor: pointer;
      }
    }
  }

  .l-button {
    width: 331px;
  }

  .tip {
    font-size: 14px;
    color: #DFE9E6;
    margin: 28px 0px 12px;
  }

  .slider-bar {
    margin: 16px 0;
    height: 16px;
  }

  .trans-summary {
    margin: 16px 0 0px 0px;
    display: none;
    opacity: 0;
    height: 0;
    animation: warnningFadeOut 1s ease-in-out;

    .trans-title {
      color: #DFE9E6;
      font-size: 14px;
    }

    .trans-body {
      margin-top: 12px;
      font-size: 12px;
      font-weight: 400;

      .trans-row {
        height: 24px;
        display: flex;
        justify-content: space-between;

        .trans-col {
          display: flex;
        }
      }
    }
  }

  .trans-summary.show {
    display: block;
    height: 100%;
    opacity: 1;
    animation: warnningfadeIn 1s ease-in;
  }

  .advanced-setting {
    margin-top: 32px;

    .toggle {
      display: inline-flex;
      font-size: 14px;
      cursor: pointer;
    }

    .slippage {
      display: none;

      .title {
        font-size: 12px;
      }

      .slippage-value {
        display: flex;
        margin-top: 16px;

        .slippage-input {
          height: 40px;
          width: 50%;
          border-radius: 2px;
          background: rgba(89, 174, 153, 0.1);
          color: #DFE9E6;

          input {
            width: 100%;
            height: 40px;
            text-align: center;
            font-size: 12px;
            padding-left: 0px;
          }

          .unit {
            position: absolute;
            left: 37%;
            line-height: 40px;
          }
        }

        .slippage-group {
          justify-content: start;
          width: 50%;

          .g-button {
            width: 30%;
            font-size: 12px;
            margin-left: 8px;
          }
        }
      }

    }

    .slippage.show {
      display: block;
    }

    .skip-confirm {
      margin: 24px 0px 0px;
      display: flex;
      align-items: center;

      .confirm-text {
        font-size: 14px;
        cursor: pointer;
      }
    }
  }

  .action.sticky {
    bottom: 0px;
    position: sticky;
  }

  .action {
    height: 80px;
    position: initial;

    .l-checkbox {
      margin-right: 8px;
    }

    .action-button {
      margin: 16px 0 24px;
      display: flex;
      justify-content: center;
    }
  }
}

