.position-card,
.history-card {
  width: 100%;
  height: 216px;
  min-width: 324px;
  background: #0D1A2E;
  border: 1px solid #203B60;
  border-radius: 6px;
  margin-top: 24px;
  padding: 18px;
  font-size: 14px;
  color: #E0ECFF;

  .market-close-header {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .market-name {
      font-size: 18px;
      font-weight: 600;
      align-items: center;
      cursor: pointer;
      display: flex;

      .symbol-name {
        display: flex;
        align-items: center;
        img{
          margin-right: 8px;
        }
      }

      .leverage-direction {
        width: 72px;
        height: 18px;
        border-radius: 5px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 14px;
        margin-left: 12px;
        border: 1px solid #CE5E5E;
        color: #CE5E5E;
      }

      .dir-long {
        border: 1px solid #59AE99;
        color: #59AE99;
      }

      .liqtion {
        border: 1px solid #FFF;
        color: #FFF;
      }
    }

    .close-position-btn {
      .close-btn {
        border: 1px solid #E0ECFF;
        border-radius: 6px;
        background-color: transparent;
      }
    }
  }

  .position-card-info-box,
  .history-card-info-box {
    width: 100%;
    margin-top: 16px;

    .position-card-info,
    .history-card-info {
      width: 100%;
      height: 24px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .position-card-info-title,
      .history-card-info-title {
        color: #93A1B8;
        font-weight: 600;
      }

      .position-card-info-value,
      .history-card-info-value {
        color: #E0ECFF;
        display: flex;
        align-items: center;

        .pnl-up {
          color: #59AE99;
        }

        .pnl-down {
          color: #CE5E5E;
        }

        img {
          margin-left: 8px;
          cursor: pointer;
        }
      }
    }
  }
}

.position-card:first-child,
.history-card:first-child {
  margin-top: 0px;
}