.gamma-what-is {
  width: 370px;
  background-color: #0A172B;
  border-radius: 4px;
  box-sizing: border-box;
  height: 600px;
  box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.5);
  padding-bottom: 24px;

  .gamma-what-is-header {
    display: flex;
    justify-content: space-between;
    color: #E0ECFF;
    align-items: center;
    font-size: 22px;
    height: 80px;
    border-bottom: 1px solid #203B60;
    padding: 0 24px;
    img{
      cursor: pointer;
    }
  }

  .gamma-what-is-body {
    padding: 24px;
    height: 520px;
    overflow: auto;

    .gamma-describe-title {
      margin-top: 24px;
    }

    .gamma-describe-text {
      margin-top: 12px;
      font-weight: 400;
      span{
        font-weight: 800;
      }
    }
  }

}