@media only screen and (min-width : 320px) {
  .symbol-panel {
    width: 100%;
  }
}

@media only screen and (min-width : 760px) {
  .symbol-panel {
    width: 404px;

  }
}

.symbol-panel {
  .sp-main {
    .tab-panel {
      .tab-header {
        margin: 8px 0px;
        width: 100%;
        height: 32px;
        font-size: 14px;

        .symbol-item {
          background: #364F70;
          border-right: 1px solid rgba(10, 23, 42, .5);
          border-bottom: 1px solid transparent;
          text-transform: uppercase;
        }

        .symbol-item:last-child {
          border-right: 0px;
        }

        .active {
          border-bottom: 1px solid #E0ECFF;
          color: #E0ECFF;
        }
      }

      .filter-tab {
        .tab-header {
          margin: 12px 40px;
          width: auto;

          .filter-item {
            background: #203B60;
            white-space: nowrap;
            height: 28px;
            border-radius: 8px;
            padding: 0 16px;
          }

          .filter-item.active {
            background: #C4C4C433;
            border: 0px;
            color: #E0ECFF;
          }

          .filter-item-bnb::after {
            content: ".";
            color: #CE5E5E;
            font-size: 34px;
            position: relative;
            top: -15px;
            left: -4px;
          }
        }
      }
    }

    .future-zone-tab,
    .option-panel-tab {
      margin-top: 24px;

      .tab-header {
        margin: auto;

        .active,
        .inactive {
          border-radius: 4px;
        }
      }
    }

    .symbol-search {
      margin-top: 8px;

      .search-token {
        flex-direction: row-reverse;
        background: #93A1B833;

        .search-input {
          font-size: 14px;
        }

        img {
          margin-right: 8px;
        }

        .search-input::placeholder {
          color: rgba(147, 161, 184, 0.5);
        }
      }
    }

    .symbol-search .search-token {
      border: 1px solid transparent;
      margin: 0 auto;
    }

    .symbol-search .search-token:hover {
      border: 1px solid #CD7A37;
    }



    .search-panel {
      border-top: 1px solid #0A172A80;
      border-bottom: 1px solid #0A172A80;

      .hot-symbol {
        margin: 8px 48px;
        background: #203B60;
        display: flex;
        justify-content: space-between;

        .hot-symbol-item {
          color: #93A1B8;
          font-size: 14px;
          border-radius: 4px;
          background: #93A1B833;
          height: 24px;
          display: flex;
          align-items: center;
          padding: 0px 4px;
          cursor: pointer;
        }
      }
    }

    .search-list {
      .category {
        margin: 24px 0px 8px 46px;
        color: rgba(147, 161, 184, 0.5);
        font-weight: 500;
        font-size: 12px;
      }
    }

    .symbols {
      .s-body {
        // min-height: 184px;
        max-height: 400px;
        overflow: auto;
        -ms-overflow-style: none;
        scrollbar-width: none;
        background: #2A4670;



        /* Firefox */
        &::-webkit-scrollbar {
          display: none;
          /* Safari and Chrome */
        }
      }

      .symbol-row:last-child {
        border-bottom: 1px solid #0A172A80;
      }

      .symbol-row {
        height: 48px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border: 1px solid transparent;
        border-top: 1px solid #0A172A80;
        padding: 0 40px;
        // padding: 0 16px;


        .ss-name {
          font-size: 16px;
          display: flex;
          min-width: 55%;
          align-items: center;
          color: #E0ECFF;
          img{
            flex-shrink:0
          }
          .m-symbol-box {
            position: relative;
            display: flex;
            justify-content: space-between;
            align-items: center;
            white-space: nowrap;

            .m-tip {
              min-width: 200px;
              display: none;
              // display: flex;
              align-items: center;
              justify-content: center;
              padding: 4px;
              font-size: 12px;
              font-weight: 400;
              background-color: #193454;
              color: #93A1B8;
              border-radius: 4px;
              // position: absolute;
              white-space: nowrap;
              height: 23px;
              margin-left: 10px;
            }
          }
          .m-symbol-box:hover{
            .m-tip {
              display: flex;
            } 
          }

          .label {
            margin-left: 4px;
          }

          img:first-child {
            margin-right: 4px;
          }

          .deri-based-tip {
            margin-left: 4px;

            .text {
              border-bottom: 0px;
            }
          }
        }

        .ss-value {
          width: 20%;
          font-size: 14px;
          display: flex;
          // align-items: center;
          flex-direction: column;
          color: #E0ECFF;

          .mark-price {
            display: flex;
            align-items: center;
            // width: 100%;
            justify-content: flex-end;
            text-align: right;
          }

          .volume24-changed {
            display: flex;
            align-items: center;
            // width: 100%;
            justify-content: flex-end;
            margin-left: 2px;
          }

          .volume24.down {
            color: #CE5E5E;
          }

          .volume24.up {
            color: #59AE99;
          }

          .direction {
            display: flex;
          }

          .volume24 {
            text-align: center;
            margin-left: 2px;
          }
        }
      }

      .symbol-row.selected {
        border: 1px solid #CD7A37;
        border-radius: 4px;
      }

      .symbol-row:hover {
        border: 1px solid rgba(205, 122, 55, 0.5);
        background-color: #5577FD1A;
        background: url(../../../../assets/img/wallet-btn-bg.png) no-repeat;
        cursor: pointer;
        border-radius: 4px;
        background-size: 100%;
      }
    }

    .option-symbols {
      color: #E0ECFF;
      font-size: 16px;

      .accordion__heading {
        height: 64px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.15);
        display: flex;
        align-items: center;
        padding-left: 16px;
        cursor: pointer;

        .accordion__button {
          width: 100%;
          height: 100%;
          display: flex;
          align-items: center;
        }

        .accordion__button:after {
          display: inline-block;
          content: '';
          height: 10px;
          width: 10px;
          margin-right: 12px;
          border-bottom: 2px solid currentColor;
          border-right: 2px solid currentColor;
          transform: rotate(-45deg);
          position: absolute;
          right: 0;
          margin-right: 24px;
        }

        .accordion__button[aria-expanded='true']::after,
        .accordion__button[aria-selected='true']::after {
          transform: rotate(45deg);
        }
      }

      .accordion__panel {
        .option-row {
          height: 56px;
          padding-left: 32px;
          display: flex;
          border: 1px solid #203B60;
          justify-content: space-between;
          align-items: center;
          margin: 0 8px;
          border-radius: 4px;
          user-select: none;

          img {
            margin: 0 4px;
          }

          .option-name {
            width: 45%;
            display: flex;
            align-items: center;
          }

          .option-value {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 55%;

            .mark-price {
              width: 45%;
            }

            .volume24 {
              width: 40%;
              text-align: center;
            }

            .direction {
              width: 15%;
            }
          }
        }

        .option-row:hover {
          border: 1px solid rgba(205, 122, 55, 0.5);
          cursor: pointer;
        }
      }
    }
  }

  .open-list {
    width: 81px;
    height: 18px;
    border: 1px solid #93A1B8;
    border-radius: 100px;
    text-align: center;
    line-height: 18px;
    display: inline-block;
  }
}

html[lang=fr] {
  .symbol-panel .open-list {
    width: 90px;
  }
}

html[lang=ja] {
  .symbol-panel .sp-main .symbols .symbol-row .ss-value {
    width: 35%;
  }

  .symbol-panel .open-list {
    width: 110px;
  }
}