.borker-bind-trade {
  width: 500px;
  height: 534px;
  // background: #0A172B;
  background-image: url('./img/bind-bg.png');
  background-size: 100% 100%;
  // border: 1px solid #203B60;
  border-radius: 10px;

  .borker-bind-modal-header {
    width: 100%;
    height: 62px;
    padding: 24px 32px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #E0ECFF;
    font-size: 18px;
    font-weight: 700;
    // border-bottom: 1px solid rgba(147, 161, 184, 0.5);

    img {
      cursor: pointer;
    }
  }

  .borker-bind-modal-info {
    width: 100%;
    margin-top: 58px;
    padding: 0 34px;
    border-bottom: 1px solid rgba(147, 161, 184, 0.5);

    .borker-bind-modal-des {
      width: 351px;
      font-weight: 400;
      font-size: 20px;
      color: #E0ECFF;
    }


    .wallet-borker-account {
      margin-top: 145px;
      font-weight: 600;
      font-size: 12px;
      margin-bottom: 45px;

      .acc-wrapper {
        width: 176px;
        height: 48px;
        background-color: transparent;
        border: 1px solid #5577FD;
        font-weight: 700;
        font-size: 16px;
        margin-top: 4px;
      }

      .acc-wrapper:hover {
        background: transparent;
      }

      .m-connect {
        width: 176px;
        height: 48px;
        font-weight: 700;
        font-size: 16px;
      }

    }

  }

  .confirm-btn-box {
    margin-top: 32px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 32px;

    .referral-code-text {
      font-weight: 600;
      font-size: 20px;
      color: #E0ECFF;
      display: flex;
      flex-direction: column;

      .referral-code-title {
        font-size: 18px;
        color: #93A1B8;
      }
    }

    .confirm-btn {
      width: 160px;
      height: 48px;
      background: #5577FD;
      border-radius: 2.65594px;
      font-weight: 700;
      font-size: 16px;
    }

    .disabled {
      background: #203B60;
      border: 1px solid #586B88;
      border-radius: 4px;
    }
  }
}

@media only screen and (max-width : 760px) {
  .borker-bind-trade {
    width: 328px;
    height: 352px;
    background-image: url('./img/bind-bg-m.png');

    .borker-bind-modal-header {
      font-size: 20px;

      img {
        width: 16px;
        height: 16px;
      }
    }

    .borker-bind-modal-info {
      margin-top: 18px;

      .borker-bind-modal-des {
        font-weight: 600;
        font-size: 14px;
        width: 230px;
      }

      .wallet-borker-account {
        margin-top: 94px;
        margin-bottom: 24px;

        .acc-wrapper,
        .m-connect {
          width: 128px;
          height: 32px;
          font-size: 12px;
        }
      }
    }

    .confirm-btn-box {
      margin-top: 24px;

      .referral-code-text {
        font-size: 14px;


        .referral-code-title {
          font-size: 14px;
          margin-bottom: 4px;
        }
      }

      .confirm-btn {
        width: 128px;
        height: 40px;
        font-size: 14px;
        border-radius: 4px;
      }
    }
  }
}