  height: 700px;
  z-index: -1;
  background-image: url('../../../../assets/img/share-position-power.svg');

  .my-address {
    position: absolute;
    right: 14px;
    top: 86px;
  }

  .symbol-name,
  .my-pnl {
    font-size: 40px;
    font-weight: 600;
    color: #E0ECFF;
    position: absolute;
    top: 410px;
    left: 100px;
  }

  .my-pnl {
    top: 495px;
  }

  .direction {
    width: 63px;
