@media only screen and (max-width : 1140px) {
  .position-container {
    .m-position {
      overflow: visible;
      height: 100%;

      .p-header {
        display: none;
      }

      .p-footer {
        display: none;
      }

      .m-potiosn-liq {
        display: block;
      }

      .p-body {
        width: 100% !important;

        .main {
          height: 100%;
          min-height: 144px;
          margin-bottom: 24px;
          max-height: initial;

          .p-position-table {
            display: none;
          }

          .m-position-card {
            display: block;

            .m-potiosn-liq {
              display: flex;
              flex-direction: column;
              justify-content: flex-end;
              align-items: flex-start;
              height: 100%;
              width: 100%;
              color: #E0ECFF;
              .liq-label {
                font-weight: 600;
                font-size: 14px;
                color: #E0ECFF;
                background-color: transparent;
                padding: 0;
              }
              .liq-item{
                display: flex;
                width: 100%;
                justify-content: space-between;
                align-items: center;
                border: 0;
                padding: 0;
                font-size: 14px;
                height: 24px;
                .nbsp{
                  display: none;
                }
                .liq-symbol-name{
                  font-weight: 600;
                  color: #93A1B8;
                  span{
                    display: none;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}